body {
  margin: 140px 0 0 0;
  padding: 0;
  font-family: 'Fjalla One', sans-serif;
  background: url('./DogBrews.jpg') no-repeat;
  background-attachment: fixed;
  width:100%;
  height:100%;
  background-size: cover;
  color: #e9e6d4;
  z-index: 1;
}

/* Responsive Adjustment for <450px */
@media only screen and (max-width: 450px) {
  body {
    width: 70%;
  }
}

